.calculator {
  width: 100%;
  max-width: 300px;
  box-shadow: 0px 0px 30px #85dce4;
  border-radius: 6px;
  overflow: hidden;
}

.calculator__row {
  width: 100%;
  display: flex;
}

.calculator__clear {
  text-align: center;
  color: crimson;
  font-size: 15px;
  cursor: pointer;
}
